<template>
  <div>
    <div class="crumbs">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item>
          <i class="el-icon-location-outline"></i> 
        </el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="container">
      <div class="borderBox">
        <div class="flex headSelect" style="margin-bottom:10px;">
          <div class="flex">
            <img class="titleIcon" src="../../../assets/img/form.png" alt />
            <div style="margin-top: -4px;    width: 74px;"></div>
          </div>

          <div class="flex funcHead">
            <div class="block">
              <span class="demonstration" style="margin-left:10px" ></span>
              <el-input style="width:140px" v-model="kName" placeholder=""></el-input>
            </div>
            <div class="block">
              <span class="demonstration" style="margin-left:10px" ></span>
              <el-select style="width:140PX" v-model="jName" placeholder="">
                <el-option
                  v-for="item in options"
                  :key="item.roleId"
                  :label="item.name"
                  :value="item.name">
                </el-option>
              </el-select>
            </div>
            <el-button
              style="margin-left:10px"
              @click="getData"
              type="primary"
              icon="el-icon-search"
            ></el-button>
            <el-button
              style="margin-left:10px"
              @click="toQrcode()"
              type="primary"
              icon="el-icon-download"
            ></el-button>
            <el-button
              style="margin-left:10px"
              type="primary"
              icon="el-icon-download"
            >
            <a style="color:#fff" href="../../../../static/file/.xls" download=".xls" ></a></el-button>
            <el-button
              style="margin-left:10px"
              @click="upload()"
              type="primary"
              icon="el-icon-upload2"
              v-if="permission==''"
            ></el-button>
            <el-button
              style="margin-left:10px"
              @click="openAdd(1)"
              type="primary"
              icon="el-icon-plus"
              v-if="permission==''"
            ></el-button>
          </div>
        </div>
        <el-table
            :data="tableData"
            style="width: 100%"
            v-loading="loading"
          >
          <el-table-column type="index" label=""  width="55"></el-table-column>
          <el-table-column prop="loginname"  label=""  ></el-table-column>
          <el-table-column prop="username"  label=""  ></el-table-column>
          <el-table-column prop="roleName"  label=""  ></el-table-column>
          <el-table-column prop="positionName"  label=""  ></el-table-column>
          <el-table-column prop="departmentName"  label=""  ></el-table-column>
          <el-table-column prop="code"  label=""   >
                <template slot-scope="scope">
                        <span class="pointer"  @click="qrCode(scope.$index, scope.row)"></span>
                 </template>
          </el-table-column>
          <!-- <el-table-column prop="opAt"  label=""   >
          </el-table-column> -->
          <el-table-column prop="time" :formatter="timeFormate"  label="" width="140"  ></el-table-column>
          <el-table-column  label="" width="150px"  > 
                <template slot-scope="scope">
                        <span class="pointer"  v-if="permission==''"  @click="detials(scope.$index, scope.row)"  ></span>
                        <span class="pointer"  v-if="permission==''" style="margin-left:10px"   @click="deal(scope.$index, scope.row)"></span>
                        <!-- <span class="pointer" style="margin-left:10px"  @click="reset(scope.$index, scope.row)"></span> -->
                 </template>
          </el-table-column>
        </el-table>
        <div class="pagination">
          <el-pagination
            background
            @current-change="handleCurrentChange"
            layout="total, prev, pager, next, jumper"
            :total="total"
          ></el-pagination>
        </div>
      </div>
    </div>

    <!--  -->
    <el-dialog :title="mTitle" :visible.sync="editVisible" width="40%"  >
        <el-form ref="ruleForm"  :model="ruleForm"  :rules="rules" label-width="120px" v-loading="loading1"  >
            <el-form-item label="" prop="loginname" required>
                  <el-input v-model="ruleForm.loginname" ></el-input>
            </el-form-item>
            <el-form-item label="" prop="password" required>
                  <el-input v-model="ruleForm.password" ></el-input>
            </el-form-item>
            <el-form-item label="" prop="username" required>
                  <el-input v-model="ruleForm.username" ></el-input>
            </el-form-item>
            <el-form-item label="" prop="jobNumber" required>
                  <el-input v-model="ruleForm.jobNumber" ></el-input>
            </el-form-item>
            <el-form-item label="" prop="roleId" required>
                   <el-select style="width:100%" v-model="ruleForm.roleId" placeholder="">
                     <el-option
                      v-for="item in options"
                      :key="item.roleId"
                      :label="item.name"
                       :value="item.roleId"
                      >
                    </el-option>
                  </el-select>
            </el-form-item>
            <el-form-item label="" prop="departmentId" required>
                   <el-select style="width:100%" v-model="ruleForm.departmentId" placeholder="">
                     <el-option
                      v-for="item in departments"
                      :key="item.id"
                      :label="item.name"
                       :value="item.id"
                      >
                    </el-option>
                  </el-select>
            </el-form-item>
             <el-form-item label="" prop="permission">
                     <el-radio v-model="ruleForm.permission" label=""></el-radio>
                     <el-radio v-model="ruleForm.permission" label=""></el-radio>
                     <el-radio v-model="ruleForm.permission" label=""></el-radio>
            </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer"  >
            <el-button @click="editVisible = false"> </el-button>
            <el-button type="primary" @click="saveEdit('ruleForm')"> </el-button>
        </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="delVisible" width="300px" center  >
        <div class="del-dialog-cnt"></div>
        <span slot="footer" class="dialog-footer">
            <el-button @click="delVisible = false"> </el-button>
            <el-button type="primary" @click="deleteRow"> </el-button>
        </span>
    </el-dialog>
   
   <!--  -->
    <el-dialog title="" :visible.sync="visible" width="40%"  >
        <el-form ref="ruleForm"   label-width="120px" v-loading="loading2"  >
            <el-form-item label=""  >
                  <el-upload
                    ref="upload"
                    class="upload-demo"
                    accept=".xls"
                    :limit="1"
                    :auto-upload="false"
                    :on-change="inputChange"
                    action="https://jsonplaceholder.typicode.com/posts/"
                   >
                    <el-button size="small" type="primary"></el-button>
                  </el-upload>
            </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer"  >
            <el-button @click="visible = false"> </el-button>
            <el-button type="primary" @click="uploads"> </el-button>
        </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="codeVisible" width="40%"  >
        <div class="flex" style="height:120px;background:#FEFF00;">
                <div style="margin-top: 4px;" ><v-Code :name="codeText" ></v-Code></div>
                <div style=" color: #000;padding-left:20px">
                    <div style="margin: 5px 0px 18px; font-size: 18px;font-weight: 600;">{{codeText.name}}</div>
                    <div></div>
                    <div>yiliaofeiwuzhuisu</div>
                </div>
        </div>
        <span slot="footer" class="dialog-footer"  >
            <el-button type="primary"  @click="codeVisible = false"> </el-button>
        </span>
    </el-dialog>

   <div class="picBox" >
        <div class="flex" v-for="(item,index) in tableData" v-bind:key="index" :id="'canvasBox'+index" style="height:120px;background:#FEFF00;width:280px;">
                <div style="margin-top: 3px;" ><canvas class="canvas" :id="'canvas'+index"></canvas></div>
                <div style=" color: #000;padding-left:20px">
                    <div style="margin: 5px 0px 18px; font-size: 18px;font-weight: 600;">{{item.name}}</div>
                    <div></div>
                    <div>yiliaofeiwuzhuisu</div>
                </div>
        </div>
    </div>
  </div>
</template>

<script>
const moment = require("moment");
import vCode from '../../common/qrcode.vue';
import QRCode from 'qrcode'
import html2canvas from 'html2canvas';
import XLSX from 'xlsx';
import { saveAs } from 'file-saver';
export default {
  data() {
    return {
      loading: false,
      loading1: false,
      loading2: false,
      delVisible: false,
      permission:false,
      visible: false,
      codeVisible: false,
      codeText:'',
      kName:'',
      jName:'',
      mTitle:'',
      tableData: [
        {
          id: 1,
          name: "",
          user:'qw12',
          time: "2018-10-08 14:44",
          name1:'121212',
          status:true,
          power:''
        }
      ],
      total: 0,
      cur_page: 1,
      editVisible:false,
      ruleForm:{
        loginname:'',
        password:'',
        username:'',
        jobNumber:'',
        departmentId:'',
        roleId:'',
        permission:'',
      },
      rules: {
          loginname: [
              { required: true, message: '' }
          ],
          password: [
              { required: true, message: '' }
          ],
          username: [
              { required: true, message: '' }
          ],
          jobNumber: [
              { required: true, message: '' }
          ],
          departmentId: [
              { required: true, message: '' }
          ],
          roleId: [
              { required: true, message: '' }
          ],
      },
      options:[
      ],
      departments:[
      ]
    };
  },
  components:{
         vCode
  },
  methods: {
    timeFormate(row){
         return moment(row.opAt).format('YYYY-MM-DD HH:mm:ss');
    },
    // 
    handleCurrentChange(val) {
      this.cur_page = val;
      this.getData();
    },
    // 
    search() {
      console.log(this.date);
    },
    toQrcode(){
          this.loading=true;
          var imgArr=[],i=this.tableData.length-1,that=this;
          setTimeout(()=>{
              this.tableData.forEach((ele,ind)=>{
                    var canvas = document.getElementById('canvas'+ind),name={id:ele.id,name:ele.username};
                    // 
                    QRCode.toCanvas(canvas, JSON.stringify(name),{width:120,height:120,margin:0,logo:'121212'}, function (error) {
                      if (error) console.error(error)
                    })
                    var canvasBox = document.getElementById('canvasBox'+ind);
                      // dombase64
                    html2canvas(canvasBox).then(function(canvas1) {
                        var fullQuality = canvas1.toDataURL("image/jpeg", 1.0);
                        fullQuality=fullQuality.substring(fullQuality.indexOf(',')+1);
                        imgArr.push({text:ele.username+'-'+new Date().getTime()+'.png',img:fullQuality});
                        if(ind==i){
                          // 
                             that.wrapImageToZip(that,imgArr,'');
                        }
                    });
              })
               this.loading=false;
          },500)
    },
    wrapImageToZip(app,strFileList, filename){
        let that = this,
          arrImages = strFileList,
          imageCount = arrImages.length,
          numCount = 0,
          arrBase64 = [];
        arrImages.map(item => {
            numCount++;
            if (numCount == imageCount) {             
              Promise.all([
                // 
                require('jszip'),
              ]).then(([JSZip, FileSaver]) => {
                let zip = new JSZip();
                let img = zip.folder('');
                arrImages.map((ele)=>{
                  img.file(ele.text,ele.img, {base64: true});
                })
                console.log(img)
                let fileName = `${filename}.zip`;
                zip.generateAsync({type: 'blob'})
                  .then(function (content) {
                    saveAs(content, fileName);
                  });
              });
            }
       });
    },
    openAdd(type,row){
      this.loading1=false;
      if(type==1){
        this.mTitle='';
        this.ruleForm={
              loginname:'',
              password:'',
              username:'',
              jobNumber:'',
              departmentId:'',
              roleId:'',
              permission:'',
        };
        if(this.$refs['ruleForm']){
           this.$refs['ruleForm'].resetFields();
        }
      }else{
        this.ruleForm.password='';
        if(this.$refs['ruleForm']){
           this.$refs['ruleForm'].resetFields();
        }
        this.mTitle='';
        var form=this.ruleForm;
        for(var key in row){
          form[key]=row[key]
        }
        this.ruleForm=form;
      }
      this.editVisible=true;
    },
    detials(index,row){
      this.openAdd(2,row);
    },
    // 
    saveEdit(formName) {
        this.$refs[formName].validate((valid) => {
            if (valid) {
              if(this.mTitle==''){
                this.add()
              }else{
                this.edit()
              }
            } else {
                return false;
            }
        });
    },
    // 
    add(){
        this.loading1=true;
        this.$axios({
              method:'post',
              url:'/platform/sys/user/addDo',
              data:this.ruleForm,
          }).then((res) =>{
              if(res.data.code==0){
              this.loading1=false;
              this.editVisible = false;
              this.getData()
              }else{
                  this.$message.error(res.data.msg);
              }
          }).catch((error) =>{
              console.log(error)    
          })
    },
     // 
    edit(){
        this.loading1=true;
        this.$axios({
              method:'put',
              url:'/platform/sys/user/editDo',
              data:this.ruleForm
          }).then((res) =>{
              if(res.data.code==0){
              this.loading1=false;
              this.editVisible = false;
              this.getData()
              }else{
                  this.$message.error(res.data.msg);
              }
          }).catch((error) =>{
              console.log(error)    
        })
    },
    // 
    deleteRow(){
        this.$axios({
              method:'DELETE',
              url:'/platform/sys/user/delete?ids='+this.id,
          }).then((res) =>{
              if(res.status==200){
                  this.$message.success('');
                  this.delVisible=false;
                  this.getData();
              }else{
                  this.$message.error(res.data.msg);
              }
          }).catch((error) =>{
              console.log(error)    
        })
    },
    // 
    allAdd(data){
        this.loading2=true;
        this.$axios({
              method:'post',
              url:'/platform/sys/user/batch',
              data:data
          }).then((res) =>{
              if(res.data.code==0){
              this.loading2=false;
              this.visible = false;
              this.$message.error('');
              this.getData()
              }else{
                  this.$message.error(res.data.msg);
              }
          }).catch((error) =>{
              console.log(error)    
          })
    },
    inputChange(file){
      const reader = new FileReader();
			reader.onload = (e) => {
				/* Parse data */
				const bstr = e.target.result;
				const wb = XLSX.read(bstr, {type:'binary'});
				/* Get first worksheet */
				const wsname = wb.SheetNames[0];
				const ws = wb.Sheets[wsname];
				/* Convert array of arrays */
				const data = XLSX.utils.sheet_to_json(ws, {header:1});
        /* Update state */
        data.shift();
        if(data[0]&&data[0].length!==7){
            this.$message('');
            this.$refs.upload.clearFiles();
            return 
        }else if(!data[0]){
            this.$message('');
            this.$refs.upload.clearFiles();
            return 
        }
        var arr=data.map((ele)=>{
            return{loginname:ele[0],password:ele[1],username:ele[2],jobNumber:ele[3],roleId:ele[4],departmentId:ele[5],permission:ele[6]==''?'':ele[6]}
        })
        this.allAdd(arr)
			};
			reader.readAsBinaryString(file.raw);
    },
    deal(index,row){
      this.id=row.id;
      this.delVisible=true;
    },
    exportCode(){
      console.log(11)
    },
    upload(){
      this.visible=true
    },
    uploads(){
      this.$refs.upload.submit();
    },
    qrCode(index,row){
        this.codeText={id:row.id,name:row.username}
        this.codeVisible=true;
    },
    statusChange(){
        
    },
    // 
    reset(){
      this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$message({
            type: 'success',
            message: '!'
          });
        }).catch(() => {         
        });
    },
    getJob(){
        this.$axios({
            method:'get',
            url:'/platform/hospital/position/data',
        }).then((res) =>{
            if(res.status==200){
                this.options=res.data.data;
            }else{
                this.$message.error(res.data.msg);
            }
        }).catch((error) =>{
            console.log(error)    
        })
        this.$axios({
            method:'get',
            url:'/platform/hospital/department/data',
        }).then((res) =>{
            if(res.status==200){
                this.departments=res.data.data;
            }else{
                this.$message.error(res.data.msg);
            }
        }).catch((error) =>{
            console.log(error)    
        })
    },
    getData(){
        this.loading=true;
        this.$axios({
            method:'get',
            url:'/platform/sys/user/listPage?pageNumber='+this.cur_page+'&pageSize=10&username='+this.kName+'&positionName='+this.jName,
        }).then((res) =>{
            if(res.status==200){
                this.loading=false;
                this.tableData=res.data.list.map((ele)=>{
                  ele.permission=ele.permission?ele.permission:'';
                  return ele
                });
                this.total=res.data.totalCount;
            }else{
                this.$message.error(res.data.msg);
            }
        }).catch((error) =>{
            console.log(error)    
        })
    },
  },
  mounted() {
    this.permission=localStorage.permission;
    this.getJob()
    this.getData()
  }
};
</script>


<style scoped>
.cloudBox > div {
  text-align: center;
  box-sizing: border-box;
  overflow: hidden;
  min-height: 300px;
  position: relative;
}

.upload-demo /deep/ .el-upload--text{
  height:46px;
  width:100%;
  padding:5px 0;
}
.picBox{
  position: absolute;
  left:-100vw;
}

</style>




